div.container {
  display: flex;
  flex-direction: column;
  width: 100%;
  max-height: 505px;
  overflow-y: auto;
  padding: 15px;
  border-bottom-left-radius: 0;
  border-top-left-radius: 0;
  @media (max-width: 900px) {
    min-height: initial;
    max-height: initial;
    margin: auto;
    border-radius: 4px;
  }
}